.nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.nav a, .nav button {
  background: #eee;
  border-radius: .2rem;
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  transition: .1s;
  cursor: pointer;
}

.nav a:hover, .nav button:hover, .nav a:focus, .nav button:focus {
  background: #fff;
  box-shadow: 0 0 0 3px #eee;
  border-color: #333;
  outline: none;
}

.nav a:global(.active) {
  background: #fff;
  box-shadow: 0 0 03px #fea;
  border-color: #fb1;
}

/* Muda a cor do svg, o sinal de maior indica que será selecionado algo dentro do svg e o asterisco que foi selecionado todos so elementos dentro do svg */
.nav a:global(.active) svg > * {
  fill: #fb1;
}

.mobileButton {
  background: #eee;
  border-radius: .2rem;
  height: 40px;
  width: 40px;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  transition: .1s;
  cursor: pointer;
  transition: .3s;
}

.mobileButton::after {
  content: '';
  display: block;
  width: 1.2rem;
  height: 2px;
  border-radius: 2px;
  background: currentColor;
  box-shadow: 0 6px currentColor, 0 -6px currentColor;
}

.mobileButton:focus, .mobileButton:hover, .mobileButtonActive {
  outline: none;
  background: #fff;
  box-shadow: 0 0 0 3px #fea;
  border-color: #fb1;
  color: #fb1;
}

.mobileButtonActive::after {
  transform: rotate(90deg);
  width: 4px;
  height: 4px;
  box-shadow: 0 8px currentColor, 0 -8px currentColor;
}

.navMobile {
  display: block;
  position: absolute;
  top: 70px;
  right: 0px;
  padding: 0 1rem;
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  border-radius: .2rem;
  transform: translateX(-10px);
  opacity: 0;
  pointer-events: none;
}

.navMobileActive {
  transition: .3s;
  transform: initial;
  opacity: 1;
  z-index: 100;
  pointer-events: initial;
}

.navMobile a, .navMobile button {
  display: flex;
  align-items: center;
  background: none;
  width: 100%;
  border: none;
  border-bottom: 1px solid #eee;
  padding: .5rem;
  cursor: pointer;
}

.navMobile svg {
  margin-right: 0.5rem;
}

.navMobile button {
  border-bottom: none;
}

.navMobile a:hover svg > *, .navMobile button:hover svg > * {
  fill: #fb1;
}